let posi;

//let mixer = null;
loader.load( 'models/gorizontal/scene.gltf', ( gltf ) => {
    
    //mixer = new THREE.AnimationMixer( gltf.scene );
    //mixer.timeScale = 0.8;//  
    //const action = mixer.clipAction( gltf.animations[0] );
    //action.play();

    posi = gltf.scene;
    scene.add( posi );
    posi.scale.set( 0.00008, 0.00008, 0.00008 );
    posi.position.set( 0.8, 0, 0 );

    let tl = gsap.timeline({
        // yes, we can add it to an entire timeline!
        scrollTrigger: {
          trigger: ".box_two",
          pin: false, // pin the trigger element while active
          start: "top top", // when the top of the trigger hits the top of the viewport
          end: ".box_three", // end after scrolling 500px beyond the start
          scrub: 6, // smooth scrubbing, takes 1 second to "catch up" to the scrollbar
          snap: {
            snapTo: "labels", // snap to the closest label in the timeline
            duration: { min: 3.5, max: 6 }, // the snap animation should be at least 0.2 seconds, but no more than 3 seconds (determined by velocity)
            delay: 0.5, // wait 0.2 seconds from the last scroll event before doing the snapping
            ease: "power4.Out", // the ease of the snap animation ("power3" by default)
          },
        },
      });

      //posi.position.set( 0, 0, 0, );
      tl
      .add("box_two", 0)
      .add("box_three", 10)
      .add("box_for", 20)
      .add("box_fife", 30)
      .add("box_six", 40)
      .add("box_sevan", 50)
    
      //tl.addLabel("spin")
      tl
      .to(posi.position, { y: -1.5, duration: 4 }, "box_two")
      .to(posi.position, { z: 20, duration: 2, delay: 0.4 }, "box_three")
      .addLabel("end");

});

