let posi4;
let mixer = null;
loader.load( 'models/clock/scene.gltf', ( planet ) => {
    
  mixer = new THREE.AnimationMixer( planet.scene );
  mixer.timeScale = 0.5;//  
  const action = mixer.clipAction( planet.animations[0] );
  action.play();

  posi4 = planet.scene;
  console.log(posi4);
  scene.add(posi4);
  posi4.scale.set( 0.1, 0.1, 0.1 );
  posi4.position.set( 0, 0, -22 );

  const clock = new THREE.Clock();

  const tick = () => {

    //controls.update();
    renderer.render(scene, camera);

    const delta = clock.getDelta();
    if ( mixer ) {
        mixer.update( delta );
    }
    window.requestAnimationFrame(tick);
};

tick();

let tl = gsap.timeline({
  // yes, we can add it to an entire timeline!
  scrollTrigger: {
    trigger: ".clock",
    pin: false, // pin the trigger element while active
    start: "top top", // when the top of the trigger hits the top of the viewport
    end: ".box_for", // end after scrolling 500px beyond the start
    scrub: 8, // smooth scrubbing, takes 1 second to "catch up" to the scrollbar
    snap: {
      snapTo: "labels", // snap to the closest label in the timeline
      duration: { min: 2.5, max: 8 }, // the snap animation should be at least 0.2 seconds, but no more than 3 seconds (determined by velocity)
      delay: 0.2, // wait 0.2 seconds from the last scroll event before doing the snapping
      ease: "power1.inOut", // the ease of the snap animation ("power3" by default)
    },
  },
});

//posi.position.set( 0, 0, 0, );
tl
.add("box_two", 0)
.add("box_three", 10)
.add("box_for", 20)
.add("box_fife", 30)
.add("box_six", 40)
.add("box_sevan", 50)

//tl.addLabel("spin")
tl
.to(posi4.position, { z: 4, y: 0.3, duration: 3 }, "box_three")
.to(posi4.scale, { x: 4, y: 4, z: 4, duration: 3 }, "box_three")
.to(posi4.position, { z: 18, duration: 1 }, "box_for")
.addLabel("end");
});

